.caroussel {
    width: 100%;
    height: 100%;
    border-radius: 0px 0px 5px 5px;
    overflow: hidden;


    @media screen and (max-width: 700px) {
        display: none;
    }

    img {
        width: 100%;
        max-height: 300px;
        height: 50vh;
        object-fit: cover;
    }
}